// Colors
// ---------------------------------

// Default

$color-default-black: #000000;
$color-default-white: #ffffff;
$color-transparent: rgba(255, 255, 255, 0);
$color-background-main: #ecf6ff;
$color-title: #0088fe;
$color-text: #252525;
$color-button: #d7e5fc;
$color-button-text: #e4eeff;
$color-button-block: #fcfcfc;
$color-text-block: #bac1c7;
$color-button-background: #e4eeff;
$color-border: #f0f3f7;
$color-input: #e3f2ff;
$color-active-button: #498ae9;
$color-subtitle: #989898;
$color-error: #ff5200;

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-neon-blue: #2c39f2;
$color-torch-red: #ff1553;
$color-limeade: #5faf2c;
$color-pelorous: #2ca7af;
$color-picton-blue: #50a8d9;
$color-viking: #48abd3;
$color-picton-blue: #4891d3;
$color-cornflower-blue: #498ae9;
$color-fiord: #535b62;

// Gradient

$black-to-right: linear-gradient(
  90deg,
  rgba(0, 0, 0, 0.75) 0%,
  rgba(0, 0, 0, 0) 75%
);
$green-to-white: linear-gradient(163deg, #a6f663 0%, #ffffff 100%);
$pelorous-to-white: linear-gradient(180deg, #c6ebdd 0%, #ffffff 100%);
$pale-turquoise-to-white: linear-gradient(180deg, #a6f4ff 0%, #ffffff 100%);
$fog-to-white: linear-gradient(180deg, #dfd2f0 0%, #ffffff 100%);
$picton-blue-to-white: linear-gradient(180deg, #b6e3fd 0%, #ffffff 100%);
$alice-blue-to-white: linear-gradient(
  153deg,
  #e7f6ff 0%,
  rgba(255, 255, 255, 0) 100%
);
$classic-rose-to-white: linear-gradient(180deg, #fccce8 0%, #ffffff 100%);
$lavender-blue-to-white: linear-gradient(180deg, #c5bcff 0%, #ffffff 100%);
// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default: 16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-manrope: "Manrope", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-375: 375px;
$vp-768: 768px;
$vp-1024: 1024px;
$vp-1280: 1280px;
$vp-1440: 1440px;
$vp-1920: 1920px;

// Retina
// ---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
